}
.button-link-container > a.button-link::before, .button-link-container > a.button-link:before, .button-link-container > a.button-link::after, .button-link-container > a.button-link:after {
  font-family: "Font Awesome 5 Free";
  line-height: 100%;
  height: 100%;
  color: #6a0e0e;
  font-size: 0.75rem;
}
.button-link-container > a.button-link::before, .button-link-container > a.button-link:before {
  padding-right: 7px;
}
.button-link-container > a.button-link::after, .button-link-container > a.button-link:after {
  padding-left: 7px;
  margin-right: 5px;
}

a.button-link[data-type=download]::before, a.button-link[data-type=download]:before {
