.document-viewer-container {
  text-align: left;
  position: relative;
  border: 3px dashed deepskyblue;
}

.article-section {
  width: 70%;
  float: left;
  border: 3px dashed brown;
}

.metrics-section {
  float: right;
  width: 30% !important;
  border: 1px solid pink;
}

.summaries-section {
  width: 30% !important;
  border: 3px dashed purple;
}

.content-section {
  display: inline-block;
}

.document-viewer-overlay {
  height: 100%;
  width: 100%;
  z-index: -10000;
  position: absolute;
  border: 3px dashed red;
}

.loader-container {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 3px solid #f6f6f6;
  border-bottom-color: #F2CA80;
  position: absolute;
  left: calc(50% - 20px);
  top: calc(50% - 20px);
  -webkit-animation: spinner 0.8s linear infinite;
          animation: spinner 0.8s linear infinite;
}

@-webkit-keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*# sourceMappingURL=DocumentViewer.css.map */

.metric-container .metric-bar-container {
  height: 5px;
  background-color: white !important;
  position: relative;
  width: 80%;
  border-radius: 4px;
  margin-top: 2px;
  border: 1px solid transparent;
}
.metric-container .metric-bar-container:first-of-type {
  border-color: rgba(0, 0, 0, 0.05);
}
.metric-container .metric-bar-container .metric-bar {
  height: 100% !important;
  border-radius: 4px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: purple;
  transition: width ease-in-out 0.1s;
}

.metric-value {
  font-weight: bolder;
  font-size: 0.85rem;
  color: #6a0e0e;
}

.metric-name {
  font-size: 0.85rem;
}

